<template>
  <v-layout row justify-center>
    <v-dialog v-model="dialog" fullscreen hide-overlay transition="dialog-bottom-transition">
      <!-- <v-btn slot="activator" color="primary" dark>Open Dialog</v-btn> -->
      <v-card>
        <v-toolbar>
          <v-btn icon @click.native="closeDialog()">
            <v-icon>close</v-icon>
          </v-btn>
          <v-toolbar-title>Take Picture</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn icon @click.native="saveImage()">
              <v-icon>save</v-icon>
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <web-camera @captured="onCapture" :face="require('../assets/face.svg')"></web-camera>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
import WebCamera from '@/components/Camera.vue'
import { EventBus } from '@/eventBus.js'
// import AmplifyStore from '../store'

export default {
  name: 'CameraDialog',
  data () {
    return {
      dialog: false,
      // userIdentityId: AmplifyStore.state.userIdentityId,
      base64Img: null
    }
  },
  created () {
    let vm = this
    EventBus.$on('dialog', function (value) {
      vm.dialog = value
      vm.base64Img = null
    })
  },
  methods: {
    closeDialog() {
      EventBus.$emit('dialog', false);
    },
    onCapture(img) {
      this.base64Img = img.split(',')[1]
      // console.log('img', img)
    },
    saveImage() {
      this.$emit('closed', this.base64Img)
      EventBus.$emit('dialog', false)
    }

  },
  components: {
    WebCamera
  }
}
</script>