<template>
  <v-stepper v-model="e1">
    <camera-dialog @closed="onCloseDialog"></camera-dialog>

    <v-stepper-header>
      <template v-for="n in steps">
        <v-stepper-step
          :complete="e1 > n"
          :key="`${n}-step`"
          :step="n"
          editable
        >
          Step {{ n }}
        </v-stepper-step>

        <v-divider
          v-if="n !== steps"
          :key="n"
        ></v-divider>
      </template>
    </v-stepper-header>

    <v-stepper-items>
      <v-stepper-content
        v-for="n in steps"
        :key="`${n}-content`"
        :step="n"
      >
      <v-layout row fill-height>
        <v-flex xs12>
          <v-card>
            <v-card-title primary-title>
              <span class="headline">{{ $t("checkIn.titleId") }} </span>
            </v-card-title>

            <v-container fluid>
              <v-layout row justify-space-between>
                <v-flex xs6>
                  <img :src="presignedUrl" class="img-responsive" height="360px" @click="showDialog(false)" style="cursor: pointer;"/>

                </v-flex>
                <v-flex xs6>
                  <img :src="require('../assets/face.svg')" class="img-responsive" height="360px" @click="showDialog(false)" style="cursor: pointer;"/>
                </v-flex>
              </v-layout>
            </v-container>

            <v-card-text>
              <div>
                <h3 class="headline mb-0">Click the face to take your picture.</h3>
                <div>Checking the passport with your picture...</div>
              </div>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
      <v-divider/>
      <v-layout row fill-height>
        <v-flex xs12>
          <v-card>
            <v-card-title>
              <span class="headline">Guest Check-in Information</span>
            </v-card-title>
            <v-card-text>
              <v-container fluid>
                <v-layout row justify-space-between>
                  <v-flex xs3>
                    <v-text-field label="*First name" required hint="*" v-model="firstName"></v-text-field>
                  </v-flex>
                  <v-flex xs3>
                    <v-text-field label="Middle name" ></v-text-field>
                  </v-flex>
                  <v-flex xs3>
                    <v-text-field label="*Last name" required hint="*" v-model="lastName"></v-text-field>
                  </v-flex>
                </v-layout>
              </v-container>
              <small>*indicates required field</small>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-2" flat @click="showDialog(false)">Take Picture</v-btn>
              <v-btn color="blue darken-1" flat @click="nextStep(n)">Next Guest</v-btn>
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>

      </v-stepper-content>
    </v-stepper-items>
  </v-stepper>
</template>

<script>
import CameraDialog from '@/components/CameraDialog.vue'
import { EventBus } from '@/eventBus.js'
import * as queries from '@/graphql/queries';
import { Storage } from 'aws-amplify'

export default {
  name: 'CheckIn',
  data () {
    return {
      e1: 1,
      steps: 1,
      guests: [],
      // lastName: '',
      // firstName: '',
      // isId: false
      imagePath: null
    }
  },
  created () {
    this.logger = new this.$Amplify.Logger('NOTES_component')
    this.getGuests();
  },
  watch: {
    steps (val) {
      if (this.e1 > val) {
        this.e1 = val
      }
    }
  },
  computed: {
    lastName: {
      get: function () {
        return this.guests[this.e1 - 1].lastName
      },
      // setter
      set: function (val) {
        // console.log(val);
        return this.guests[this.e1 - 1].lastName = val
      }
    },
    firstName: {
      get: function () {
        return this.guests[this.e1 - 1].firstName
      },
      // setter
      set: function (val) {
        return this.guests[this.e1 - 1].firstName = val
      }
    }
  },
  asyncComputed: {
    presignedUrl: async function() {
      this.imagePath = (this.e1 - 1) + '.jpg'
      let url = await Storage.get(this.imagePath, {level: 'private'})
      return url
    }
  },
  methods: {
    nextStep (n) {
      if (n === this.steps) {
        this.e1 = 1
      } else {
        this.e1 = n + 1
      }
    },
    showDialog(isId) {
      EventBus.$emit('dialog', true);
      this.isId = isId;
    },
    onCloseDialog(base64Img) {

      if (base64Img) {
        Storage.put((this.e1 - 1)+ '.jpg', new Buffer(base64Img, 'base64'), {
          level: 'private',
          contentType: 'image/jpeg'
        })
        .then (result => console.log(result))
        .catch(err => console.log(err))        
      }


    },
    getGuests() {
      // let guests = await this.$Amplify.API.graphql(this.$Amplify.graphqlOperation(queries.getReservationGuest, { $reservationCode: 'H1234'}))
      // console.log(guests);

      this.$Amplify.API.graphql(this.$Amplify.graphqlOperation(queries.getReservationGuest, { reservationCode: 'H1234' }))
      .then((res) => {
        // console.log(res.data.getReservationGuest)
        this.logger.info(`guests successfully listed`, res.data.getReservationGuest)
        this.steps = res.data.getReservationGuest.length
        this.guests = res.data.getReservationGuest
      })
      .catch((e) => {
        this.logger.error(`Error listing Todos`, e)
      });
    }

  },
  components: {
    cameraDialog: CameraDialog
  }
}
</script>