import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    user: null,
    userIdentityId: null
  },
  mutations: {
    setUser(state, user) {
      state.user = user
    },
    setUserIdentityId(state, userIdentityId) {
      state.userIdentityId = userIdentityId
    }
  },
  actions: {

  }
})

export default store